 
import React, { useState } from "react";
import css from "./style.module.css";
import { Link } from "react-router-dom";
import Cevap from './cevap'

const style = {
  position: 'absolute',
  // display:'flex',
  // alignItems:'center',
  // justifyContent:'center',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 350,
  bgcolor: 'black',
  // border: '2px solid #000',
  boxShadow: 44,
//   p: 4,
  margin:' auto',
  padding:'320px 15px'
};

export default function Research(props) {
  const [soru1, setSoru1] = useState("");
  const [soru2, setSoru2] = useState("");
  const [soru3, setSoru3] = useState("");
  const [soru4, setSoru4] = useState("");
  const [soru5, setSoru5] = useState("");
  const [soru6, setSoru6] = useState("");
  // const [too, setToo] = useState(0);
  const [open, setOpen] = useState(false);

  // const doruCevap = ['Kahve yapyorlar', 'Leylann evinin karsnda', 'Drde yirmi gee','Gl Sokak','On dokuz','Yan sokakta']
  const data = [
    {
      id: 1,
      soru: "Leyla ve arkadalar kafede ne yapmyor?",
      cevap: ["Kahve yapyorlar", "Tatl yiyorlar", "Ders alyorlar",'Kahve iiyorlar'],
    },
    {
      id: 2,
      soru: "Metne gre market nerede?",
      cevap: ["Leylann evinin yannda", "Leylann evinin altnda", "Leylann evinin yaknnda",'Leylann evinin karsnda'],
    },
    {
      id: 3,
      soru: "Onlar saat kata spor salonuna gidiyorlar?",
      cevap: ["Bei yirmi kala", "Drde yirmi gee", "Drde bei kala",'Alty yirmi gee'],
    },
    {
      id: 4,
      soru: "Leylann evi hangi sokakta?",
      cevap: ["Blbl Sokak", "Smbl Sokak", "Gl Sokak",'Divan Sokak'],
    },
    {
      id: 5,
      soru: "Leyla ka yanda?",
      cevap: ["On yedi", "Otuz", "Yirmi dokuz",'On dokuz'],
    },
    {
      id: 6,
      soru: "Metne gre Divan Kafe nerede?",
      cevap: ["Smbl Sokakta", "Marketin yannda", "Yan sokakta",'Evin karsnda'],
    },
  ];

  const Clicked = (e, id) => {
    switch (id) {
      case 1: setSoru1(e); break;
      case 2: setSoru2(e); break;
      case 3: setSoru3(e); break;
      case 4: setSoru4(e); break;
      case 5: setSoru5(e); break;
      case 6: setSoru6(e); break;
      default: break;
    }
  };

    const handleClose = () => {
      setOpen(false);
    };

  const Submit = () =>{
    props.setMessName("warning");
    if(soru1 === ''){
      props.setMess(true);
      props.setMessText('soru 1 cevaplaynz !!!');
    } else if (soru2 === ''){
      props.setMess(true);
      props.setMessText('soru 2 cevaplaynz !!!');
    } else if (soru3 === ''){
      props.setMess(true);
      props.setMessText('soru 3 cevaplaynz !!!');
    } else if (soru4 === ''){
      props.setMess(true);
      props.setMessText('soru 4 cevaplaynz !!!');
    } else if (soru5 === ''){
      props.setMess(true);
      props.setMessText('soru 5 cevaplaynz !!!');
    } else if (soru6 === ''){
      props.setMess(true);
      props.setMessText('soru 6 cevaplaynz !!!');
    } else {

      // let cevap =  [soru1, soru2, soru3, soru4, soru5, soru6];
      // for (let i = 0; i < doruCevap.length; i++) {
      //   doruCevap[i] === cevap[i] && too++;
      //   // if (doruCevap[i] === cevap[i]) setToo(too + 1);
      //   }
        setOpen(true);
    }
   
        
  }

  
  

  return (
    <div className={css.sec}>
      <section  >
      <Link to=''><button className={css.subClose}>Kapat</button></Link>
      <div className={css.cards}><p>Merhaba, benim adm Leyla. 19 yandaym. Ailem Edirnede yayor. Ben Sakaryada yalnz yayorum. Evimde yatak odas, salon, mutfak, balkon ve banyo var. Evim Gl Sokakta. Evimin karsnda bir market var. Bu marketten alveri yapyorum. Marketin yannda bir spor salonu var. Komum ile beraber spor salonuna gidiyoruz. Her gn 16.20den 17.20ye kadar spor yapyoruz. Yan sokan ad Blbl Sokak. Burada bir kafe var. Kafenin ad Divan Kafe. ok gzel ve ok sakin bir yer. Orada bazen tatl yiyoruz, bazen kahve iiyoruz, bazen ders alyoruz. Arkadalarmn evi benim evimin yaknnda. Onlar Smbl Sokakta oturuyor. Biz genellikle beraber vakit geiriyoruz. Evimi, sokam, arkadalarm ve Sakaryay ok seviyorum.</p></div>
      <div className={css.cards}><h5>Aadaki sorular metne gre cevaplaynz.</h5></div>
      {data.map((el) => (
       <div className={css.cards}><div key={el.id}>
          
            <h5>{el.soru}</h5>
            {el.cevap.map((e, i) => (
              <button
                key={i}
                onClick={() => Clicked(e, el.id)}
                className={ soru1 === e || soru2 === e ||  soru3 === e ||  soru4 === e || soru5 === e || soru6 === e
                    ? css.cevapActive
                    : css.cevap
                }
              >{e}</button>
            ))}
          </div>
        </div>
      ))}
      </section>
      {/* <buttonclassName={css.submit}>Sinav</buttonclassName=> */}
      <Cevap open={open} close={handleClose}  onClick={Submit}  cevap={[soru1, soru2, soru3, soru4, soru5, soru6]} doruCevap={['Kahve yapyorlar', 'Leylann evinin karsnda', 'Drde yirmi gee','Gl Sokak','On dokuz','Yan sokakta']}/>
    </div>
  );
}
