import React  from 'react';
import css from "./style.module.css"
import { BiArrowBack } from "react-icons/bi";
import { Link } from 'react-router-dom';
import Voice from "../voice";
// ,BiPlayCircle
const Dinleme = () => {
  return (
    <div>
      <div className={css.head}>
        <div className={css.head1}>
          <Link to="/A1">
            <BiArrowBack className={css.icon1} />
          </Link>
          &nbsp;&nbsp;Dinleme
        </div>
          <input className={css.inputSearch} type="search" placeholder="Search..."/>
      </div>
      <section className={css.section}>
       <label><h1 className={css.h22}> <Voice voice={`BABAM,.,., ,

Orta boylu, geni omuzlu, byk bal, yuvarlak yzl, kaln kara kal, ela gzl, irice bykldr. Srma ilemeli yelekteki dmelerinin ou, yaz k zktr; iri, kaln bilekleri vardr. Belinde bir al vardr. Pek yakkl, dolgun vcutlu bir insandr, ama iman deildir.

Onun ahlkna dair birka sz syleyeyim: Kimseye fenalk etmez, pek ok kiiye iyilik eder. Doruluk, mertlik kendisine babasndan miras. Biraz hiddetlidir. Dnyada hi kimseye muhta olmadan yaamay mutluluk sayar ve kendi ileriyle megul olur.

Ufak bir dkkn vardr. Sabahleyin erkenden dkknna gider. Akam st doruca evine gelir. Hi iki  imez, sigara kullanmaz. Evimizi kendisi yaptrd. Evine ok iyi bakar. Her eyin iyisini satn almak ister. O kk ev, byk bir saadet yeridir.`} />&nbsp;&nbsp;&nbsp;BABAM</h1><p><br />

Orta boylu, geni omuzlu, byk bal, yuvarlak yzl, kaln kara kal, ela gzl, irice bykldr. Srma ilemeli yelekteki dmelerinin ou, yaz k zktr; iri, kaln bilekleri vardr. Belinde bir al vardr. Pek yakkl, dolgun vcutlu bir insandr, ama iman deildir.

Onun ahlkna dair birka sz syleyeyim: Kimseye fenalk etmez, pek ok kiiye iyilik eder. Doruluk, mertlik kendisine babasndan miras. Biraz hiddetlidir. Dnyada hi kimseye muhta olmadan yaamay mutluluk sayar ve kendi ileriyle megul olur.

Ufak bir dkkn vardr. Sabahleyin erkenden dkknna gider. Akam st doruca evine gelir. Hi iki  imez, sigara kullanmaz. Evimizi kendisi yaptrd. Evine ok iyi bakar. Her eyin iyisini satn almak ister. O kk ev, byk bir saadet yeridir.</p></label><hr />
      </section>
    </div>
  );
};
/* <BiPlayCircle/>&nbsp; */
export default Dinleme;