import React from "react";
import css from "./style.module.css";
import { Link } from "react-router-dom";
import Image from "../../assets/bigLogo.png";

const GetStarted = () => {
  return (
    <section className={css.all}>
      <img className={css.Logo} src={Image} alt="logo" />
      <div className={css.gol}>
        <b className={css.text}>
          <h1 className={css.h1}>Yardmc</h1>
          <p className={css.pa}>
            stediiniz zaman ve istediiniz yerde dil renin. cretsiz ve
            sonsuza kadar.
          </p>
        </b>

        <hr className={css.hr} />
        <div className={css.buttons}>
          <Link to="/">
            <button type="button" className={css.button1}>
              BALAMAK
            </button>
          </Link>
          <br />
          <Link to="/login">
            <button className={css.button2}>ZATEN HESABIM VAR</button>
          </Link>
        </div>
      </div>
    </section>
  );
};
export default GetStarted;
